<template>
  <div>
    <van-sticky>
      <van-nav-bar title="积分转账" left-arrow @click-left="onClickLeft" right-text="转账记录"   @click-right="onClickRight" />
    </van-sticky>
      <div class="header1">
      <div style="font-size:16px;">{{usermess.totalIntegral.toFixed(1)}}</div>
      <div style="font-size:16px;">当前余额</div>
    </div>
    <div class="userWrap">
    <!-- <van-field label="验证码" placeholder="请输入验证码" v-model="userInfo.pid_member"  /> -->
    <van-cell-group>
    <van-field label="转账账号" placeholder="请输入对方账号" v-model="userInfo.member"  />
    <van-field label="转账数量" placeholder="请输入转账数量" v-model="userInfo.nums"  />
      </van-cell-group>
      <button class="submitBtn" @click="updateUser">提交</button>
    </div> 
        <van-dialog
      v-model="showPassword"
      title="交易密码"
      show-cancel-button
      @confirm="pay"
      @cancel="cancelPay"
      :before-close="beforePasswordClose"
    >
      <van-password-input :value="password" info="密码为 6 位数字类型" style="position:initial" />
    </van-dialog>
    <van-number-keyboard
      id="keyboard-panel"
      :show="showPassword"
      @input="onInput"
      @delete="onDelete"
      :z-index="9999"
      style="color:black"
    />
  </div>
</template>

<script>
import { transferInfo, uploadImg, transferAction } from "@/api/personal";
export default {
  data() {
    return {
    usermess:{},
      userInfo: {
        member: "",
        nums: "",
        transaction:""
      },
      showPassword: false,
      password: '',
      img: ""

    };
  },
  created() {
    this.getUser();
  },
  methods: {
      onClickRight(){
          this.$router.push({name:"waifmoney"})
      },
          beforePasswordClose(action, done) {
      if (action === "confirm") {
        if (this.password.length != 6) {
          done(false);
        } else {
          done();
        }
      } else {
        done();
      }
    },
    onInput(key) {
      this.password = (this.password + key).slice(0, 6);
      this.zIndex = 9999;
    },
    onDelete() {
      this.password = this.password.slice(0, this.password.length - 1);
    },
    cancelPay() {
      this.password = "";
    },
    pay() {
        this.userInfo.transaction=this.password
             transferAction(this.userInfo).then(res => {
          if(res.errcode==0){
            this.$toast('转账成功');
            this.password = "";
          }else{
            this.$toast(res.msg);
          }
            this.getUser();
      });

    },
    getUser() {
    transferInfo().then(res=>{
        this.usermess=res
    })
    },
    onClickLeft() {
        this.$router.go(-1);
    },
    upImg(file) {
        this.image = file.content.substring(file.content.indexOf(",") + 1);
      uploadImg(this.image, "").then(resp => {
        this.userInfo.wechat_img = resp.img;
      });
    },
    // 提交
    updateUser() {
        this.showPassword=true
    //   transferAction(this.userInfo).then(res => {
    //       console.log(res)
    //       if(res.errcode==0){
    //       this.$toast('转账成功');

    //       }else{
    //       this.$toast(res.msg);

    //       }
    //         this.getUser();

    //   });
    }
  }
};
</script>
<style lang="scss" scoped>
.van-nav-bar__text{
    color: white;
}
/deep/ .van-dialog{
    top:35%;position: fixed;
    .van-dialog__header{
        height: 45px;
    }
    .van-password-input__security{
        margin-bottom: 10px;
    }
    .van-password-input__info{
        padding-bottom: 20px;
    }
}
 .header1 {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 55px;
    color: black;
    background: #ffeee7;
    padding-bottom: 10px;
  }
/deep/ .van-field__label{
    width: 120px;
}
.van-button--primary{
     background: white;
     color: #28A6FF;
     border: 1px solid #28A6FF;
     border-radius: 20px;
}
.van-cell {
  padding: 15px 16px 19px;
}
.userImg {
  width: 39px;
  height: 39px;
}
.submitBtn {
  display: block;
  width: 345px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  font-size: 18px;
  border-radius: 20px;
  background: #28A6FF;
  margin: 66px auto;
  border: 0 none;
}
</style>
